<template>
	<view class="container">
		<ClassTitle tit="中职专业" path="/pages/major/major" />
		<MajorList :listData="listDataFn" />
		<ClassMore path="/pages/major/major" />
	</view>
</template>

<script>
	import ClassTitle from '../library/ClassTitle.vue'
	import MajorList from '../MajorList.vue'
	import {
		getMajorList
	} from '@/api/major.js'
	import {
		websiteUrl
	} from '@/utils/config.js'
	import ClassMore from '@/components/library/ClassMore.vue'
	export default {
		components: {
			MajorList,
			ClassTitle,
			ClassMore
		},
		data() {
			return {
				listData: [],
			}
		},
		created() {
			getMajorList().then(res => {
				this.listData = res.data.rows
			})
		},
		computed: {
			listDataFn() {
				return this.listData.map(item => {
					return {
						title: item.title,
						titlepic: websiteUrl + item.titlepic,
						smalltext: item.smalltext.substring(0, 33) + '...',
						id: item.id
					}
				})
			}
		},
		methods: {
			getMajorListFn() {
				getMajorList().then(({
					data
				}) => {
					data.rows.map(item => {
						item.titlepic = websiteUrl + item.titlepic
						item.smalltext = item.smalltext.substring(0, 30) + '...'
						return item
					})
					this.listData = data.rows
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.container {
		margin-top: 20px;
	}
</style>
